<template>
  <v-list flat class="pt-0">

    <draggable
      v-model="tasks"
      handle=".handle"
    >

    <tasks
      v-for="task in tasks"
      :key="task.id"
      :task="task"
    />

    </draggable>

  </v-list>
</template>

<script>
import draggable from 'vuedraggable'

export default {
  components: {
    'tasks': require('./Task').default,
    draggable
  },
  computed: {
    tasks: {
      get() {
        return this.$store.getters.tasksFiltered
      },
      set(value) {
        this.$store.dispatch('setTasks', value)
      }
    }
  }
}
</script>
