<template>
  <div>
    <v-menu bottom left>
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark icon v-bind="attrs" v-on="on">
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>

      <v-list>
        <v-list-item
          v-for="(item, index) in items"
          :key="index"
          @click="handleClick(index)"
          >
          <v-list-item-icon>
            <v-icon v-text="item.icon"></v-icon>
          </v-list-item-icon>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>

    <dialog-due-date v-if="dialogs.dueDate" :task="task" @close="dialogs.dueDate = false" />
    <dialog-edit v-if="dialogs.edit" :task="task" @close="dialogs.edit = false" />
    <dialog-delete v-if="dialogs.delete" :task="task" @close="dialogs.delete = false" />    

  </div>
</template>

<script>
export default {
  props: ["task"],

  data: () => ({
    items: [
      { title: "Due date",
        icon: "mdi-calendar",
        click() {
          this.dialogs.dueDate = true
        }
      },
      { title: "Edit",
        icon: "mdi-pencil",
        click() {
          this.dialogs.edit = true
        }
      },
      { title: "Delete",
        icon: "mdi-delete",
        click() {
          this.dialogs.delete = true
        }
      },
      { title: "Sort",
        icon: "mdi-drag-horizontal-variant",
        click() {
          if (!this.$store.state.search) {
            this.$store.commit('toggleSorting')
          } else {
            this.$store.commit('showSnackbar', 'Cannot sort while searching.')
          }
        }
      }
    ],

    dialogs: {
        dueDate: false,
        edit: false,
        delete: false        
    }
  }),

  methods: {
    handleClick(index) {
      this.items[index].click.call(this)
    }
  },

  components: {
    "dialog-due-date": require("../Todo/Dialogs/DialogDueDate").default,
    "dialog-edit": require("../Todo/Dialogs/DialogEdit").default,
    "dialog-delete": require("../Todo/Dialogs/DialogDelete").default    
  },
};
</script>

<style></style>
